.mobile_fixed_menu::after {
  height: 6%;
  width: 35%;
  bottom: 10%;
  content: " ";
  position: absolute;
  border-radius: 1em;
  background-color: #f2f3f4; }

.menu__item {
  all: unset;
  flex-grow: 1;
  display: flex !important;
  cursor: pointer;
  overflow: hidden;
  padding-top: 0.5em;
  position: relative;
  align-items: center !important;
  color: var(--color-1);
  justify-content: center !important;
  transition: flex-grow var(--duration) var(--cubic); }

.menu__icon {
  font-size: 1em;
  stroke: currentColor;
  transition: transform var(--duration) var(--cubic); }

.menu__item::before {
  top: 5%;
  left: 14.4%;
  width: 1.5em;
  height: 1.5em;
  content: " ";
  position: absolute;
  border-radius: 50%;
  transform: scale(0);
  transform-origin: center;
  background-color: #fdecef;
  transition: transform var(--duration) var(--cubic); }

.menu__item::after {
  left: 0;
  bottom: 0;
  content: " ";
  height: 0.25em;
  position: absolute;
  border-radius: 2em;
  transform-origin: left center;
  background-color: currentColor;
  width: calc(var(--lineWidth) + 5px);
  transform: translate3d(3em, 0, 0) scaleX(0);
  transition: transform calc(var(--duration) + .2s) var(--cubic); }

.menu__text {
  left: 4.15em;
  font-size: 1.4em;
  position: absolute;
  text-transform: capitalize;
  letter-spacing: .01em;
  transform: translate3d(0, 150%, 0);
  transition: transform calc(var(--duration) / 3.7); }

.menu__item.menu__item_active {
  flex-grow: 1.5;
  display: flex;
  justify-content: flex-start !important;
  color: var(--color-2); }

.menu__item.menu__item_active .menu__icon {
  transform: translate3d(100%, 0, 0); }

.menu__item.menu__item_active::before {
  transform: scale(0.8); }

.menu__item.menu__item_active::after {
  transform: translate3d(5.9em, 0, 0) scaleX(0.94);
  transition: transform var(--duration) var(--cubic); }

.menu__text.menu__item_active {
  transform: translate3d(0, 0, 0);
  transition: transform calc(var(--duration) / 1.5); }

.icon {
  --duration-icon: 1s;
  fill: none;
  width: 2.5em;
  height: 2.5em;
  display: block;
  stroke-width: 15;
  stroke-miterlimit: 10; }

